.header {
    background-color: $clr1;

    .header-container {
        grid-column: 2;
        column-gap: 30px
    }

    .nav-bar {
        font-size: 18px;
    }
    
    .headline {
        column-gap: 80px;
        h1 {
            font-size: 100px;
            min-width: 588px;
        }

        p {
            align-self: flex-end;
            max-width: 445px;
            font-size: 18px;
            line-height: 28px;
        }
    }
}